export default {
  name: "Contact",
  head () {
    return {
      title: 'Amanda Graham - Contact',
    }
  },
  async asyncData({ $prismic, error }) {
    try {
      const contact = (await $prismic.api.getSingle('contact')).data

      return {
        contact
      };
    } catch (e) {
      error({ statusCode: 404, message: "Page not found" });
    }
  }
}
